img {
  width: 80px;
}

.player1 {
  margin: 0;
}

button {
  margin: 0;
  border: 0;
  padding: 0;
}

.hand {
  display: flex;
}

.card {
  margin: 5px;
}

.cardimage {
  margin: 0;
  width: 80px;
}


.board {
  background-color: rgb(25, 50, 75);
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.info {
  display: flex;
  align-items: center;
  justify-content: center;
}

.infoItem {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  width: 25em;
}

html, body, #root {
  height: 100%;
  width: 100%;
  margin: 0;
  color: white;
  font-family: Garamond, Georgia, 'Times New Roman', Times, serif;
}

div.disabled {
  pointer-events: none;
}

.messageHTML {
  display: flex;
  justify-content: left;
  flex-direction: column;
}

.message {
  margin: 0;
  /* display: flex;
  justify-content: left; */
}
